import { Component } from '@angular/core';

import { Th2MatToastService } from 'thin2-components';
import { Th2NovaToastService } from '../../../utils/components/th2-nova-toast/th2-nova-toast.service';

@Component({
    selector: 'toast-view-component',
    templateUrl: 'toast-view.component.html'
})
export class DemoToastComponent {
    constructor(private toastService: Th2NovaToastService) {}

    public sendToast() {
        const infoToastConfig1 = {
            description: `Lorem Ipsum is simply dummy text of the printing and typesetting 
                industry. Lorem Ipsum has been the industry's standard dummy text ever since 
                the 1500s, when an unknown printer took a galley of type and scrambled it to 
                make a type specimen book. It has survived not only five centuries, but also 
                the leap into electronic typesetting, remaining essentially unchanged.`,
            error: 'Error message in case required',
            title: 'Information message title',
            type: 'info',
            time: 0,
            links: [
                {
                    id: 'linkId1',
                    label: 'Ver detalles'
                },
                {
                    id: 'linkId2',
                    label: 'Cancelar'
                }
            ]
        };
        const infoToastConfig2 = {
            description: `Description of the information to communicate.`,
            title: `Information message title. Prueba de titulo de gran longitud para comprobar 
                visualización.`,
            type: 'info',
            time: 0
        };
        const infoToastConfig3 = {
            description: `Description of the information to communicate.`,
            type: 'info',
            time: 0
        };
        const successToastConfig = {
            description: 'Description of the information to communicate.',
            error: 'Error message in case required',
            title: 'Success message title',
            type: 'success',
            time: 0
        };
        const warningToastConfig = {
            description: 'Description of the information to communicate.',
            error: 'Error message in case required',
            title: 'Warning message title',
            type: 'warning',
            time: 0
        };
        const errorToastConfig = {
            description: 'Description of the information to communicate.',
            error: 'Error message in case required',
            title: 'Error message title',
            type: 'error',
            time: 0
        };

        this.toastService.openToast(infoToastConfig1);
        this.toastService.openToast(infoToastConfig2);
        this.toastService.openToast(infoToastConfig3);
        this.toastService.openToast(successToastConfig);
        this.toastService.openToast(warningToastConfig);
        this.toastService.openToast(errorToastConfig);
    }
}
